import { Injectable, Injector } from '@angular/core';
import { EMPTY, of } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { BefRepository } from '@farris/bef';
import { FrameContext, DataPathCreator, Repository, DataPath, EntityList, BindingList, Entity } from '@farris/devkit';
import { LanguageService } from './languag.service';
import { FormMessageService } from './form-message.service';

@Injectable()
export class PopUpService {
  constructor(
    private injector: Injector,
    private frameContext: FrameContext,
    private repository: Repository<any>,
    private languageService: LanguageService,
    private messageService: FormMessageService
  ) { }
  public confirm() { }
  /**
   * 
   * @param frameId
   * @param id
   * @returns
   * @throws invalid frameid
   */
  public cancel(frameId: string, id?: string) {
    const frameContext = this.frameContext.appContext.frameContextManager.getFrameContextById(frameId);
    if (!frameContext) {
      throw new Error(`[PopUpService]Invalid frameId ${frameId}`);
    }
    const primaryValue = this.frameContext.bindingData.list.currentId;
    const bindingPath = frameContext.viewModel.bindingPath;
    const bindingPaths = bindingPath.split('/').filter((p) => p);
    if (!id) {
      const bindingList = this.frameContext.bindingData.getList() as BindingList;
      id = bindingList.currentId;
    }
    const befRepository = this.repository as BefRepository<any>;
    const longPaths = (DataPathCreator.createByShortPathFromRoot(bindingPaths, befRepository.entityManager, this.frameContext.bindingData) as DataPath).toArray().map((path: string) => path.split(':')[1]);
    const entityListPaths = Array.from(longPaths);
    // 
    entityListPaths.pop();
    const dialogRef = this.frameContext.frameComponent['dialogRef'];
    if (entityListPaths.length < 1) {
      // 
      const entity = this.repository.entityCollection.getEntityById(id) as Entity;
      const originalData = entity['originalData'];
      entity.load(originalData, { loadChild: false });
    } else {
      const entityList = befRepository.entityManager.getEntityNodeByPath(entityListPaths) as EntityList<any>;
      if (entityList) {
        const originalData: any[] = entityList['originalData'];
        const item = originalData.find((item) => item.id === id);
        if (item) {
          // 
          const entity: Entity = befRepository.entityManager.getEntityByPath(longPaths) as Entity;
          if (entity.changes && entity.changes.length > 0) {
            return this.messageService.confirm(this.languageService.cancelWithoutSave).pipe(
              tap((result: boolean) => {
                if (result) {
                  entity.load(item, { loadChild: false });
                  entity.changes.splice(0, entity.changes.length);
                }
              })
            );
          } else {
            // 
            if (dialogRef) {
              dialogRef.close();
            }
          }
        } else {
          // 
          const paths = this.buildPath(bindingPath, primaryValue);
          return this.messageService.confirm(this.languageService.cancelWithoutSave).pipe(
            switchMap((result) => {
              if (result) {
                return befRepository.removeEntityByPath(paths, id).pipe(
                  tap(() => {
                    befRepository.entityManager.removeEntityByPath(paths, id);

                    if (entityList.count() === 0 && dialogRef) {
                      dialogRef.close();
                    }
                  })
                );
              } else {
                return EMPTY;
              }
            })
          );
        }
      }
    }
    return of([]);
  }
  /**
   * 
   * @param id 
   */
  public updateCurrentRow(id?: string) {
    const bindingPath = this.frameContext.viewModel.bindingPath;
    const bindingPaths = bindingPath.split('/').filter((p) => p);
    // const frameId = this.frameContext.frameId;
    const root = this.frameContext.getVirtualRootFrameContext();
    const primaryKeyValue = root.bindingData.list.currentId;
    this.frameContext.bindingData.list.setCurrentId(primaryKeyValue);
    if (bindingPaths.length > 0) {
      const paths = [];
      bindingPaths.forEach((path: string, index: number, array) => {
        paths.push(path);
        const bindingList = root.bindingData.getValue(paths) as BindingList;
        if (bindingList) {
          const currentId = bindingList.currentId;
          const modalBindingList = this.frameContext.bindingData.getValue(paths) as BindingList;
          if (index === bindingPath.length - 1 && id) {
            modalBindingList.setCurrentId(id);
          } else if (modalBindingList) {
            modalBindingList.setCurrentId(currentId);
          }
        }
      });
    }
  }
  public closeCheck() {
    const frameContext = this.frameContext;
    const bindingPath = frameContext.viewModel.bindingPath;
    const bindingPaths = bindingPath.split('/').filter((p) => p);
    const befRepository = this.repository as BefRepository<any>;
    const longPaths = (DataPathCreator.createByShortPathFromRoot(bindingPaths, befRepository.entityManager, this.frameContext.bindingData) as DataPath).toArray().map((path: string) => path.split(':')[1]);
    const entityListPaths = Array.from(longPaths);
    entityListPaths.pop();
    const entityList = befRepository.entityManager.getEntityNodeByPath(entityListPaths) as EntityList<any>;
    const dialogRef = this.frameContext.frameComponent['dialogRef'];
    if (entityList.count() === 0 && dialogRef) {
      dialogRef.close();
    }
  }
  /**
   * 
   * @param frameId -id
   * @param id -idid
   * @param showConfirm -`true`
   * @returns
   * @throws id
   */
  public removeRow(frameId?: string, id?: string, showConfirm: string | boolean = true) {
    const frameContext = frameId ? this.frameContext.appContext.frameContextManager.getFrameContextById(frameId) : this.frameContext;
    if (!frameContext) {
      throw new Error(`id${frameId}`);
    }
    // 
    if (!id) {
      const bindingList = frameContext.bindingData.getList() as BindingList;
      id = bindingList.currentId;
    }
    const bindingPath = frameContext.viewModel.bindingPath;
    const bindingPaths = bindingPath.split('/').filter((p) => p);
    const paths = this.buildPath(bindingPath, this.frameContext.bindingData.list.currentId);
    const befRepository = this.repository as BefRepository<any>;
    // showConfirm
    if (typeof showConfirm == 'string') {
      showConfirm = showConfirm.trim() === 'false' ? false : true;
    }
    let confirm$ = of(true);
    if (showConfirm) {
      confirm$ = this.messageService.confirm(this.languageService.confirmDeletion);
    }
    return confirm$.pipe(
      switchMap((result) => {
        if (result) {
          if (bindingPaths.length === 0) {
            // 
            return befRepository.removeEntityById(id, false);
          } else {
            return befRepository.removeEntityByPath(paths, id).pipe(
              tap(() => {
                befRepository.entityManager.removeEntityByPath(paths, id);
              })
            );
          }
        } else {
          return EMPTY;
        }
      })
    );
  }
  /**
   * 
   * @param frameId -id
   * @throws id
   */
  public closeDialog(frameId?: string) {
    let frameContext = this.frameContext;
    let dialogRef = null;
    // id
    if (frameId) {
      frameContext = this.frameContext.appContext.frameContextManager.getFrameContextById(frameId);
      if (!frameContext) {
        throw new Error('id');
      }
      dialogRef = frameContext.frameComponent['dialogRef'];
    } else {
      // 
      dialogRef = frameContext.frameComponent['dialogRef'];
      if (!dialogRef) {
        // 
        dialogRef = this.frameContext.parent.frameComponent['dialogRef'];
      }
    }
    if (!dialogRef) {
      throw new Error(``);
    }
    dialogRef.close();
  }
  /**
   * 
   * @param frameId - id
   * @param id - id
   * @param showConfirm - 
   */
  public cancelChanges(frameId?: string, id?: string, showConfirm: boolean | string = true) {
    const frameContext = this.getFrameContext(frameId);
    // 
    id = this.getPrimaryValue(frameId, id);
    const data = this.getPersisteData(frameId, id);
    if (typeof showConfirm === 'string') {
      showConfirm = showConfirm.trim().toLowerCase() === 'false' ? false : true;
    }
    const isDataChanged = this.isDataChanged(data, frameId, id);
    let result$ = of(true);
    if (showConfirm && isDataChanged) {
      result$ = this.messageService.confirm(this.languageService.cancelWithoutSave);
    }
    return result$.pipe(
      switchMap((result: boolean) => {
        if (result) {
          const befRepository = frameContext.repository as BefRepository<any>;
          const bindingPaths = frameContext.viewModel.bindingPath.split('/').filter((p) => p);
          const longPaths = (DataPathCreator.createByShortPathFromRoot(bindingPaths, befRepository.entityManager, this.frameContext.bindingData) as DataPath).toArray().map((path: string) => path.split(':')[1]);
          const entityPaths = Array.from(longPaths);
          const entity = befRepository.entityManager.getEntityNodeByPath(entityPaths) as Entity;
          if (entity) {
            entity.load(data);
          }
          return of(true);
        } else {
          return EMPTY;
        }
      })
    );
  }
  /**
   * 
   * @param frameId - id
   * @param id - id
   */
  public persiste(frameId?: string, id?: string) {
    // 
    const bindingObject = this.getBindingObject(frameId, id);
    bindingObject['__PSESISTE__DATA'] = bindingObject.toJSON();
  }
  /**
   * 
   * @param frameId - id
   * @param id - id
   * @returns
   */
  private getPersisteData(frameId?: string, id?: string) {
    const bindingObject = this.getBindingObject(frameId, id);
    return bindingObject['__PSESISTE__DATA'];
  }
  private isDataChanged(data: { [prop: string]: any }, frameId?: string, id?: string) {
    const bindingObject = this.getBindingObject(frameId, id);
    const value = bindingObject.toJSON();
    return JSON.stringify(value) !== JSON.stringify(data);
  }
  /**
   * 
   * @param frameId - id
   * @param id - id
   * @returns
   * @throws 
   */
  private getBindingObject(frameId?: string, id?: string) {
    const frameContext = this.getFrameContext(frameId);
    // 
    id = this.getPrimaryValue(frameId, id);
    const bindingPaths = frameContext.viewModel.bindingPath.split('/').filter((p) => p);
    // 
    const bindingList: BindingList = frameContext.bindingData.getValue(bindingPaths) as BindingList;
    if (!bindingList || bindingList.length < 1) {
      throw new Error(`id:${id}`);
    }
    return bindingList.currentItem;
  }
  /**
   * 
   * @param bindingPath - 
   * @param id - id
   * @throws 
   */
  private buildPath(bindingPath: string, id: any) {
    let path = '/' + id;
    const subPaths = bindingPath.split('/');
    if (subPaths.length > 0) {
      // eg:bindingPath/edus/grades,split['', 'edus', 'grades']
      // index1
      for (let index = 1; index < subPaths.length - 1; index++) {
        const subPath = subPaths[index];
        const subData = this.frameContext.viewModel.bindingData[subPath];
        if (!subData || !subData.currentId) {
          throw Error(`${subData}`);
        }
        path += `/${subPath}/${subData.currentId}`;
      }
    }
    path += '/' + subPaths[subPaths.length - 1];
    return path;
  }
  /**
   * id
   * @param frameId - id
   * @returns
   * @throws id
   */
  private getFrameContext(frameId?: string) {
    const frameContext = frameId ? this.frameContext.appContext.frameContextManager.getFrameContextById(frameId) : this.frameContext;
    if (!frameContext) {
      throw new Error(`id${frameId}`);
    }
    return frameContext;
  }
  /**
   * 
   * @param frameId - id
   * @param id - id
   * @returns
   * @throws id
   */
  private getPrimaryValue(frameId?: string, id?: string) {
    if (!id) {
      const frameContext = this.getFrameContext(frameId);
      if (!frameContext) {
        throw new Error(`id${frameId}`);
      }
      const bindingList = frameContext.bindingData.getList() as BindingList;
      if (!bindingList || bindingList.length < 1) {
        throw new Error(`:${id}`);
      }
      id = bindingList.currentId;
    }
    return id;
  }
}
